export let isbn13 = (str: string | Array<any>| any) =>{

    let str10 = str.split("");
    let str13 = str.split("");
    let isValid = false;
    let str10Container = [];
    console.log(str10.length);
    
    if(str10.length === 10){
        let counterIsbn10 = 11;
        for (let i =0 ; str10.length > i ;i++){

            str10Container[i] = str10[i] * (counterIsbn10--);
            
            
        }
        isValid = str10Container.reduce((a,b) => a + b) % 11 === 0;
    
        return isValid ? "Valid":"Invalid"
    }


    

    
}

